<template lang="docs">
	# Modal 

	## 

	 `slot` `header` `default` `footer` `header` `default` `footer` 

	:::html
		<Button
			@click="show = !show"
			type="primary"
		>
			Toggle modal box
		</Button>
		<Modal v-model="show">
			<!--header-->
			<h2 slot="header">Hello world!</h2>
			<!--default-->
			<div>
				<p>Proin eget tortor risus.</p>
			</div>
			<!--footer-->
			<template slot="footer">
				<Button
					@click="show = false"
					type="primary"
					ghost
				>
					
				</Button>
				<Button
					@click="onConfirm"
					type="primary"
				>
					
				</Button>
			</template>
		</Modal>
	:::

	`Alert` `Confirm` `Prompt`

	## Alert 

	`Alert` `info` `success` `danger` `warning` `Message` 

	<Button @click="alert('info')">
		info
	</Button>
	<Button @click="alert('success')" type="primary">
		success
	</Button>
	<Button @click="alert('danger')" type="danger">
		danger
	</Button>
	<Button @click="alert('warning')" type="warning">
		warning
	</Button>

	```javascript
		alert (type = 'info') {
			this.$alert({
				type: type,
				title: type,
				content: '<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. </p><p>Curabitur aliquet quam id dui posuere blandit.</p>',
				confirmText: 'OK',
				onConfirm: () => {
					this.$message({
						text: 'confirm'
					})
				}
			})
		},
	```

	## Confirm 

	

	<Button @click="confirm">
		$confirm
	</Button>

	```javascript
		confirm () {
			this.$confirm({
				title: 'Jane Eyre',
				content: '',
				confirmText: 'Yes',
				cancelText: 'No',
				onConfirm: () => {
					this.$message({
						type: 'danger',
						text: 'Yes'
					})
				},
				onCancel: () => {
					this.$message({
						type: 'success',
						text: 'No'
					})
				}
			})
		}
	```

	## Prompt 

	

	<Button @click="prompt">
		$prompt
	</Button>

	```javascript
		prompt () {
			this.$prompt({
				title: 'Your name:',
				onConfirm: (value) => {
					this.$message({
						type: 'success',
						text: `Hello ${value}`
					})
				},
				onCancel: () => {
					this.$message({
						text: 'canceled'
					})
				}
			})
		}
	```

	## API

	### Alert

	|||||
	|---|---|---|---|
	|type|`info` `success` `danger` `warning`|String|`info`|
	|title||String|`''`|
	|content||String||
	|confirmText||String|`''`|
	|onConfirm||Function|`undefined`|

	### Confirm

	|||||
	|---|---|---|---|
	|title||String|`''`|
	|content||String||
	|confirmText||String|`''`|
	|cancelText||String|`''`|
	|onConfirm||Function|`undefined`|
	|onCancel||Function|`undefined`|

	### Prompt

	|||||
	|---|---|---|---|
	|title||String|`''`|
	|content||String||
	|confirmText||String|`''`|
	|cancelText||String|`''`|
	|onConfirm||Function|`undefined`|
	|onCancel||Function|`undefined`|
</template>

<script>
	export default {
		data () {
			return {
				show: false
			}
		},
		methods: {
			onConfirm () {
				this.show = false
				this.$message({
					type: 'success',
					text: 'confirmed ',
					duration: 2000
				})
			},
			alert (type = 'info') {
				this.$alert({
					type: type,
					title: type,
					content: '<p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. </p><p>Curabitur aliquet quam id dui posuere blandit.</p>',
					confirmText: 'OK',
					onConfirm: () => {
						this.$message({
							text: 'confirm'
						})
					}
				})
			},
			confirm () {
				this.$confirm({
					title: 'Jane Eyre',
					content: '',
					confirmText: 'Yes',
					cancelText: 'No',
					onConfirm: () => {
						this.$message({
							type: 'danger',
							text: 'Yes'
						})
					},
					onCancel: () => {
						this.$message({
							type: 'success',
							text: 'No'
						})
					}
				})
			},
			prompt () {
				this.$prompt({
					title: 'Your name:',
					// TODO: required: true,
					onConfirm: (value) => {
						this.$message({
							type: 'success',
							text: `Hello ${value}`
						})
					},
					onCancel: () => {
						this.$message({
							text: 'canceled'
						})
					}
				})
			}
		}
	}
</script>



