<template lang="docs">
	# Checkbox 

	## 

	`value`  `Boolean``value1: {{value1}}`
	:::html
		<Checkbox
			v-model="value1"
		>A</Checkbox>
	:::

	## 

	`Checkbox` 

	:::html
		<Checkbox
			v-model="value1"
			disabled
		>A</Checkbox>
	:::

	## 

	 `CheckboxGroup` `value` `value2: {{value2}}`

	:::html
		<CheckboxGroup
			v-model="value2"
		>
			<Checkbox label="A">A</Checkbox>
			<Checkbox label="B">B</Checkbox>
			<Checkbox label="C">C</Checkbox>
		</CheckboxGroup>
	:::

	### 

	:::html
		<CheckboxGroup
			v-model="value2"
			disabled
		>
			<Checkbox label="A">A</Checkbox>
			<Checkbox label="B">B</Checkbox>
			<Checkbox label="C">C</Checkbox>
		</CheckboxGroup>
	:::

</template>

<script>
	export default {
		data () {
			return {
				value1: false,
				value2: []
			}
		}
	}
</script>

<style>
	.o-Input {
		max-width: 300px;
	}
</style>



