<template lang="docs">

	# Pagination 

	## 

	<InputNumber v-model="currentPage"/>

	:::html
		<Pagination
			@current-change="onPageChange"
			:current-page="currentPage"
			:page-size="listing.limit"
			:total="listing.total"
			layout="prev, pager, next"
			:formatter="linkFormatter"
			target="_blank"
		></Pagination>
	:::


	```javascript
		export default {
			data () {
				return {
					currentPage: 1,
					listing: {
						limit: 10,
						total: 255
					}
				}
			},
			methods: {
				onPageChange (page) {
					this.currentPage = page
					console.log(page)
				},
				linkFormatter (page) {
					return `/baike/${page}`
				}
			}
		}
	```

	## API

	|||||
	|---|---|---|---|
	|currentPage||Number|`1`|
	|pageSize||Number|`10`|
	|total||Number|`0`|
	|layout| ~~~~|String|`''`|
	|fomatter| SSR|Function|`undefined`|
	|nativeLink| `<a>`|Boolean|`false`|
	|step||Number|`10`|
	|prevStep||String|``|
	|nextStep||String|``|
	|prevText||String|``|
	|nextText||String|``|
</template>

<script>
	export default {
		data () {
			return {
				currentPage: 1,
				listing: {
					limit: 10,
					total: 255
				}
			}
		},
		methods: {
			onPageChange (page) {
				this.currentPage = page
				console.log(page)
			},
			linkFormatter (page) {
				return `/baike/${page}`
			}
		}
	}
</script>

<style scoped>
	.o-Input {
		width: 100px;
	}
</style>



