<template lang="docs">
	# Message 

	## 

	message  3 

	:::html
		<Button
			@click="showMessage"
			type="primary"
		>
			show message
		</Button>
	:::

	```javascript
		methods: {
			showMessage () {
				this.$message({
					text: `${this.text}`,
					duration: 5000,
					type: this.type
				})
			}
		}
	```

	## API

	|||||
	|---|---|---|---|
	|type|`info``danger``warning``success`|String|`info`
	|duration|`ms`|Number|3000
	|text||String||
</template>

<script>
	export default {
		data () {
			return {
				text: 'message'
			}
		},
		methods: {
			showMessage () {
				this.$message({
					text: `${this.text}`,
					duration: 5000,
				})
			}
		}
	}
</script>



