<template lang="docs">
	# Input 

	## 

	

	:::html
		<Input
			placeholder="placeholder"
			v-model="inputText"
			type="password"
		/>
	:::

	## 

	 `Input`  `slot`  `addonBefore`  `addonAfter`

	<!-- <Input
		v-model="inputText"
		:options="inputTextOptions"
	>
		<InputSelect
			v-model="inputText"
			slot="addonBefore"
			:options="inputTextOptions"
		/>
		<Button slot="addonAfter">Go!</Button>
	</Input> -->

	:::html
		<Input
			v-model="inputText"
			:options="inputTextOptions"
		>
			<i slot="addonBefore" class="iconfont icon-link" style="color: #888;"></i>
			<Button slot="addonAfter">Go!</Button>
		</Input>
	:::

	##  `Input` 

	:::html
		<div>
			<Input
				v-model="inputText"
				size="lg"
				placeholder=""
			></Input>
			<Input
				v-model="inputText"
				size="md"
				placeholder=""
			></Input>
			<Input
				v-model="inputText"
				size="sm"
				placeholder=""
			></Input>
		</div>
	:::

	## 

	`Input` 

	:::html
		<Input
			v-model="inputText"
			readonly
		></Input>
	:::

	## 

	`Input`  `form` 

	:::html
		<Input
			v-model="inputText"
			disabled
		></Input>
	:::

	## 

	 `options`  `InputSelect` 

	:::html
		<Input
			v-model="inputText"
			:options="inputTextOptions"
		></Input>
	:::

	## API

	|||||
	|---|---|---|---|
	|size||String|`'md'`|
	|options||String|`undefined`|
	|readonly||Boolean|`false`|
	|disabled||Boolean|`false`|
</template>

<script>
	export default {
		data () {
			return {
				inputText: '',
				inputTextOptions: ['Alice', 'Bob', 'Eve']
			}
		}
	}
</script>

<style scoped>
	.o-Input {
		max-width: 300px;
	}

	.o-Input:not(:last-of-type) {
		margin-bottom: 1rem;
	}
</style>



