<template lang="docs">
	# Select 

	## 

	 `options`  `item`  `Number``String`  `key === value`

	:::html
		<Select
			v-model="value"
			:options="['Alice', 'Bob', 'Dylan']"
		></Select>
	:::

	 `value`  `Object` 

	```JavaScript
		options = [
			{
				key: 'key',
				value: {}
			}
		]
	```

	

	<span>value: {{value}}</span>

	:::html
		<Select
			v-model="value"
			:options="[
				{
					key: '',
					value: 1
				},
				{
					key: '',
					value: 2
				}
			]"
		></Select>
	:::

</template>

<script>
	export default {
		data () {
			return {
				value: 1,
			}
		}
	}
</script>

<style scoped>
	.o-Input {
		max-width: 200px;
	}

	.o-Input:not(:last-of-type) {
		margin-bottom: 1rem;
	}
</style>



