<template lang="docs">
	# Popup 

	## 

	 `content`  `Popup`  `default slot` 

	`v-popup`  `ref` 

	:::html
		<Popup
			action="click"
			title="title"
			content="content"
			ref="popup"
		>
			<p>Hello World!</p>
		</Popup>
		<Button
			v-popup:popup
		>
			show Popup!
		</Button>
	:::

	## API

	|||||
	|---|---|---|---|
	|title||String|`''`|
	|content||String|`''`|
	|content||String||
	|v-popup|required `popup` |String|`''`|
</template>

<script>
	export default {}
</script>



