<template lang="docs">
	# 

	## 

	:::html
		<InputDate
			v-model="date"
		></InputDate>
	:::

	## 

	:::html
		<InputTime
			v-model="time"
		></InputTime>
	:::

	## API

	|||||
	|---|---|---|---|
