\t\t\tonSuccess(res)\n\t\t\t\t}).catch(err => {\n\t\t\t\t\tonError(err)\n\t\t\t\t})\n\t\t\t},\n\t\t\tbeforeUpload (files) {\n\t\t\t\t// check files\n\t\t\t\treturn true\n\t\t\t},\n\t\t\tonCancel () {\n\t\t\t\tthis.$message({\n\t\t\t\t\ttype: 'error',\n\t\t\t\t\ttext: 'cancel'\n\t\t\t\t})\n\t\t\t},\n\t\t\tonProgress (e) {\n\t\t\t},\n\t\t\tonError (e) {\n\t\t\t\tthis.status = 'danger'\n\t\t\t},\n\t\t\tonSuccess (e) {\n\t\t\t\tthis.$message({\n\t\t\t\t\ttype: 'success',\n\t\t\t\t\ttext: 'success'\n\t\t\t\t})\n\t\t\t}\n\t\t}\n\t}\n</script>\n\n<style>\n\t.upload-demo {\n\t\twidth: 420px;\n\t}\n</style>\n"],"sourceRoot":""}]);

